body {
    font-family: 'Inter', sans-serif;
    margin: 0px;
}

.wpui_header a {
    text-decoration: none;
}

.wpui_hero_Section {
    background-image: url('/src/img/banner.webp');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 10vw 0px;
}

.wpui_newsletter {
    padding: 60px 20px;
}

.wpui_newsletter .nl_container {
    width: 50%;
    margin: auto;
}

.wpui_newsletter .nl_container button {
    background: #D9D9D9;
    border-radius: 4px;
    color: #5A6475;
    height: auto;
}

.wpui_newsletter .nl_container button:hover {
    background-color: #D9D9D9 !important;
    color: #5A6475 !important;
}

.wpui_newsletter .nl_container button:focus {
    --wp-admin-theme-color: #d9d9d9;
}

.wpui_newsletter .nl_container input:focus {
    box-shadow: 0 0 0 0.5px #5A6475;
}

.wpui_newsletter .nl_container .nl_input {
    width: 50%;
}

.wpui_newsletter .nl_container .nl_input input {
    border-radius: 4px;
    border: 1px solid #D9D9D9;
    padding: 12px;
    background: transparent
}

.wpui_newsletter .nl_container .nl_input * {
    height: 100%;
}

.resize_cont .components-resizable-box__handle-right::after {
    width: 6px;
    height: 35px;
    border-radius: 4px;
    border: 0px;
    box-shadow: none;
    background: #94A3B8;
    top: 50%;
    right: calc(50% - 14px);
    transform: translateY(-50%);
}

.resize_cont .components-resizable-box__handle-right:before {
    display: none !important;
}

.resize_cont .components-resizable-box__handle-right {
    cursor: ew-resize !important;
}

.wpui_footer {
    background-color: #3858E9 !important;
}

.wpui_header,
.wpui_footer .components-card__body {
    padding: 20px 3vw;
}

.wpui_footer .components-card__body:nth-child(1) {
    padding: 60px 3vw;
}

.wpui_footer .components-card__body .sec_1 {
    width: 50%;
    margin: auto;
}

.wpui_lcont,
.wpui_com_page {
    max-width: 1350px;
    margin: auto;
    padding: 40px 20px;
}

div.wpui_preview {
    height: 180px;
    border-radius: 4px;
}

div.wpui_cl .components-navigator-button {
    height: auto;
    box-shadow: none !important;
    padding: 10px;
    border-radius: 10px;
}

.wpui_copy {
    box-shadow: none !important;
}

.copied_pop .components-popover__content {
    padding: 8px !important;
}

@media only screen and (max-width: 480px) {
    div.wpui_cl {
        grid-template-columns: repeat(1, 1fr);
        column-gap: 10px;
    }

    .wpui_footer .components-card__body .sec_1 {
        width: 100% !important;
    }

    div.wpui_newsletter .nl_container {
        width: 100%;
    }
}

@media only screen and (min-width: 481px) and (max-width: 820px) {
    div.wpui_cl {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 10px;
    }
}